<template>
  <!-- 我的商品 -->
  <div
    class="coupon"
    v-infinite-scroll="loadMore"
    infinite-scroll-disabled="loading"
    infinite-scroll-distance="10"
  >
    <div class="coupon-list">
      <div class="coupon-box" v-for="(item, index) of listData" :key="index">
        <div class="coupon-item">
          <div class="hd">
            <div class="price">
              <span class="label">¥</span>
              <span class="num">{{ item.price }}</span>
            </div>
            <div class="name">{{ item.name }}</div>
          </div>
          <div class="bd">
            <div class="sn">卡号：{{ item.card_no }}</div>
            <div class="key">卡密：{{ item.card_ps }}</div>
            <div class="link">消费链接：{{ item.url }}</div>
            <div class="date">{{ item.create_time }}</div>
            <div class="remark">{{ item.worth }}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="rule"><span @click="viewRule">商品规则</span></div>

    <Modal class="modal" :visible.sync="isModalShow" title="商品规则">
      <div class="modal-content" slot="content">
        <div class="text mb-24">
          {{ruleText}}
        </div>
      </div>
    </Modal>
  </div>
</template>

<script>
import Modal from '@/components/commonModal'

export default {
  name: 'Coupon',
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      isModalShow: false, // 将消耗XX兑换
      pageNum: 1,
      pageSize: 20,
      listData: [],
      loading: false,
      isLoadedAll: false,
      ruleText: ''
    }
  },
  components: {
    Modal
  },
  created () {
    this.getList()
    this.getRule()
  },
  methods: {
    viewRule () {
      this.isModalShow = true
    },
    async getList () {
      const res = await this.$request('get', '/user/getmygoodspagelist', {
        uid: this.userInfo.uid,
        page_num: this.pageNum,
        page_size: this.pageSize
      })
      const list = res.data.list || []
      this.listData = [...this.listData, ...list]
    },
    async loadMore () {
      if (this.isLoadedAll) {
        return
      }
      this.loading = true
      this.pageNum++
      const res = await this.$request('get', '/user/getmygoodspagelist', {
        uid: this.userInfo.uid,
        page_num: this.pageNum,
        page_size: this.pageSize
      })
      const list = res.data.list || []
      this.listData = [...this.listData, ...list]
      if (res.data.list.length === 0) {
        this.isLoadedAll = true
      }
      this.loading = false
    },
    async getRule () {
      const res = await this.$request('get', '/activity/getrule', {
        type: '4'
      })
      this.ruleText = res.data
    }
  }
}
</script>

<style lang="less" scoped>
.coupon {
  min-height: 100vh;
  background-color: #f8f8f8;
  .coupon-list {
    padding: 1px 0;
    .coupon-box {
      box-sizing: border-box;
      padding: 10px;
      width: 686px;
      height: 216px;
      box-shadow: 0 0 8px 8px #f6e7bd;
      background-color: #b62d0d;
      border-radius: 12px;
      margin: 28px auto;
      .coupon-item {
        width: 666px;
        height: 196px;
        border-radius: 12px;
        background-color: #dd4f2c;
        background-image: url(~@/assets/images/coupon/coupon_bg@2x.png);
        background-size: 646px 176px;
        background-repeat: no-repeat;
        background-position: center;
        position: relative;
        display: flex;
        padding: 10px;
        color: #a83f23;
        &::before {
          content: ' ';
          display: inline-block;
          height: 130px;
          width: 2px;
          background-image: url(~@/assets/images/coupon/coupon_bg_line@2x.png);
          background-repeat: no-repeat;
          background-size: 100%;
          position: absolute;
          left: 198px;
          top: 38px;
        }
        .hd {
          flex: none;
          width: 190px;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          .price {
            margin-bottom: 10px;
            .label {
              font-size: 44px;
            }
            .num {
              font-size: 72px;
            }
          }
          .name {
            font-size: 24px;
          }
        }
        .bd {
          flex: none;
          width: 456px;
          font-size: 24px;
          padding-left: 29px;
          padding-top: 25px;
          position: relative;
          .sn,
          .key,
          .link {
            margin-bottom: 10px;
          }
          .date {
            font-size: 16px;
            margin-top: 20px;
          }
          .remark {
            position: absolute;
            top: 25px;
            right: 25px;
            font-size: 16px;
          }
        }
      }
    }
  }
  .rule {
    text-align: center;
    font-size: 28px;
    color: #333333;
    margin-top: 100px;
    padding-bottom: 60px;
  }
  .modal-content {
    .text {
      text-align: left;
    }
  }
}
</style>



